import React, { useEffect } from 'react'
import { graphql } from 'gatsby'
import { Helmet } from 'react-helmet'

import Toc from '../components/toc'
import Layout from '../components/layout'
import MyJumbotron from '../components/jumbotron'

export default blogComponent

function blogComponent({ data, location }) {
  const blog = data.markdownRemark

  const title = `Blog de lyceum | ${blog.frontmatter.title}`
  const description = `Un article de ${blog.frontmatter.author} intitulé: «${blog.frontmatter.title}» publié sur le blog de lyceum, un site libre pour le lycée.`

  // importe le web-component de wikimage
  useEffect(() => {
    import(`wc-wikimage`)
  }, [])

  return (
    <Layout location={location} data={data}>
      <Helmet
        title={`${title}`}
        meta={[
          {
            name: `description`,
            description: `${description}`,
          },
          {
            name: `keywords`,
            content: `blog`,
          },
        ]}
      ></Helmet>
      <MyJumbotron>
        <h1>{`${blog.frontmatter.title}`}</h1>
        {blog.tocHTML && blog.tocHTML.length > 0 && (
          <Toc tableOfContents={blog.tocHTML} />
        )}
      </MyJumbotron>
      <div dangerouslySetInnerHTML={{ __html: blog.html }} />
    </Layout>
  )
}

export const query = graphql`
  query MDblogQuery($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      timeToRead
      fields {
        slug
      }
      frontmatter {
        author
        tags
        title
        date
        tocHTML
      }
    }
  }
`
